.PublicContent {
  margin-top: 70px;
}

.initialContent {
  display: flex;
  flex-direction: column;
}

.initialContent h3,
.CategoryPost h3 {
  color: rgb(202, 202, 202);
  border-bottom: 1px solid rgb(202, 202, 202);
  padding: 0 10px 10px 10px;
  margin: 0 10px 10px 10px;
}

.categoryList-container {
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat( 3, 1fr );
  padding-bottom: 20px;
}